import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './dashboard.css';
import Component from './Component';
import Cross from './Cross';
import refreshIcon from '../images/loading.png'; // Adjust the path if needed


function Dashboard() {
  const [names, setNames] = useState([]);
  const [dict, setDict] = useState({});
  const [activeScreen, setActiveScreen] = useState(null);
  const [goal, setGoal] = useState('');
  const [y_data, sety_data] = useState([]);
  const [prev_goal, set_prev_goal] = useState('');
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [refreshKey, setRefreshKey] = useState(0); // default key


  useEffect(() => {
    // Function to load data from localStorage and setNames
    const loadFromLocalStorage = () => {
      const dictString = localStorage.getItem('miles2');
      const dict = JSON.parse(dictString)
      const data = dict.conversation_data
      console.log(data)
      setNames(Object.keys(data));
    };

    // Load data when the component mounts
    loadFromLocalStorage();

    // Set up an event listener to update the component when the 'miles' key changes in localStorage
    const handleStorageChange = (e) => {
      if (e.key === 'miles2') {
        loadFromLocalStorage();
      }
    };
    window.addEventListener('storage', handleStorageChange);

    // Clean up the event listener when the component unmounts
    return () => {
      window.removeEventListener('storage', handleStorageChange);
    };
  }, []);

  const handleClick = (name) => { //show the contents of the name
    setActiveScreen(name);
    setGoal('')
    const dictString = localStorage.getItem('miles2');
    const dict = JSON.parse(dictString)
    const data = dict.conversation_data
    sety_data(data[name][2])
    set_prev_goal(data[name][0])
    // sety_data([20,40])

  };

  const handleGoalSet = (e) => { //set goal
    e.preventDefault(); // Prevent the default form submission
    const dictString = localStorage.getItem('miles2');
    const dict = JSON.parse(dictString)
    const data = dict.conversation_data
    console.log(`Goal Set: ${goal}`);
    data[activeScreen][0] = goal;
    data[activeScreen][2] = []; //reset the y values
    set_prev_goal(goal);
    dict.conversation_data = data; // Updating the original dict with the modified data
    localStorage.setItem('miles2', JSON.stringify(dict));

    setGoal("");
    setRefreshKey(refreshKey + 1);
    // You can do something with the goal here, such as sending it to a server
    // or updating a state that holds goals
  };


  const refreshdata = () => {
    setIsRefreshing(true); // Start rotating the image
    const dictString = localStorage.getItem('miles2');
    const dict = JSON.parse(dictString);
    const data = dict.conversation_data;
    console.log("3################")
    console.log(data[activeScreen][1]);
    console.log(data[activeScreen][3]);
    console.log("3################")
    if(data[activeScreen][1] == data[activeScreen][3]){
      // same chats, no update.
      console.log("same Chats");
      setIsRefreshing(false);
      return;
    }
    axios.post('https://miles.gamhcrew.repl.co/get_health', {
      // y_values, prev_chats, new_chats, goal, name
      y_values: data[activeScreen][2],
      prev_chats: data[activeScreen][1], //ye wali last goal set pe aayi hain
      new_chats: data[activeScreen][3], //ye hai bc nayi wali chats
      goal: data[activeScreen][0],
      name: dict.user_name

    })
      .then((response) => {
        // Check if the name exists in the conversation_data
        if (data[activeScreen]) {
          console.log("got health")
          // Append the value 0 to the array at the specified name
          data[activeScreen][2].push(response.data.y_value);
          console.log(response.data.y_value)
          console.log(data[activeScreen][2])
          sety_data(data[activeScreen][2])

          // Save the updated object back to local storage
          localStorage.setItem('miles2', JSON.stringify(dict));
          console.log('Value appended successfully');
        } else {
          console.log('Name not found in conversation_data');
        }
      })
      .catch((error) => {
        console.log(error);
      });


    handleClick(activeScreen);

    // Simulating a delay for the refresh operation (you can replace this with an actual API call or other logic)
    setTimeout(() => {
      setIsRefreshing(false); // Stop rotating the image
    }, 1000); // Adjust the duration as needed
  };


  return (

    <div className="grandfatherbox" >
      <div className="Dashboard">
        <div className="Sidebar">
          {names.map((name, index) => (
            <div key={name}>
              <button
                className={name === activeScreen ? "active" : ""}
                onClick={() => handleClick(name)}
              >
                {name}
                <button
                  className='cross_button'
                  style={{ borderRadius: "5px", width: '10px', height: '10px', display: 'flex', alignItems: 'center', justifyContent: 'center', backgroundColor: "transparent" }}
                  onClick={(e) => {
                    e.stopPropagation(); // Prevent the outer button's onClick from firing
                    const dictString = localStorage.getItem('miles2');
                    const dict = JSON.parse(dictString)
                    const data = dict.conversation_data
                    const name_to_be_removed = names[index]
                    delete data[name_to_be_removed];
                    dict.conversation_data = data; // Updating the original dict with the modified data
                    localStorage.setItem('miles2', JSON.stringify(dict));
                    const newNames = [...names];
                    newNames.splice(index, 1);
                    setNames(newNames);
                    if (activeScreen === name_to_be_removed) {
                      setActiveScreen(null)
                    }

                  }}
                >
                  <Cross />
                </button>
              </button>
            </div>
          ))}

        </div>

        <div className="MainContent">
          {activeScreen && (
            <>
              <div className="ActiveName">{activeScreen}</div>
              <div className="GoalInput">
                <form onSubmit={handleGoalSet}>
                  <input
                    type="text"
                    value={goal}
                    onChange={e => setGoal(e.target.value)}
                    placeholder={prev_goal}
                  />
                  <button type="submit">Set Goal</button>
                </form>

              </div>
              <div className='graphcontent' style={{ display: 'flex', 'flexDirection': 'column', alignItems: 'flex-end' }}>
                <button style={{ background: 'transparent' }} onClick={refreshdata} className={isRefreshing ? "refreshing" : ""}>
                  <img src={refreshIcon} alt="Refresh" className="refresh-icon" style={{ width: '24px', height: '24px' }} />
                </button>

                <Component data={y_data} refreshKey={refreshKey} />
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default Dashboard;
