import React from 'react';

export default function Cross() {
  const crossStyle = {
    width: '10px',
    height: '10px',
    position: 'relative',
  };

  const lineStyle = {
    position: 'absolute',
    width: '1px',
    height: '8px',
    backgroundColor: '#C5C5D3',
  };

  return (
    <div style={crossStyle}>
      <div style={{ ...lineStyle, transform: 'rotate(45deg)' }} />
      <div style={{ ...lineStyle, transform: 'rotate(-45deg)' }} />
    </div>
  );
}
