import React from 'react';
import * as d3 from 'd3';
import { useRef, useEffect } from 'react';

const Component = ({ data }, {refreshKey}) => {
  const svgRef = useRef();

  const xMin = 0;
  const xMax = 19; // Fixed to 20 marks
  const yMin = 0;
  const yMax = 100; // Fixed to 100
  const margin = { top: 20, right: 20, bottom: 30, left: 40 };
  const width = 800;
  const height = 300;
  const circleRadius = 6;
  const displayData = data.slice(-20); // Take the last 10 elements

  useEffect(() => {
    const svg = d3.select(svgRef.current);

    // Clear previous content
    svg.selectAll('*').remove();

    const xScale = d3.scaleLinear()
      .domain([xMin, xMax])
      .range([margin.left, width - margin.right])
      .nice(); // Makes the axis nice and rounded

    const yScale = d3.scaleLinear()
      .domain([yMin, yMax])
      .range([height - margin.bottom, margin.top]);

    const line = d3.line()
      .x((_, i) => xScale(i))
      .y(d => yScale(d));

    svg.append("path")
      .datum(displayData)
      .attr("d", line)
      .attr("stroke", "white")
      .attr("fill", "none")
      .attr("opacity", "30%");

    const xAxis = d3.axisBottom(xScale)
      .ticks(20); // 20 ticks for the x-axis

    svg.append("g")
      .attr("transform", `translate(0, ${height - margin.bottom})`)
      .call(xAxis);

    // Only add the y-axis if there are 10 or more elements
      svg.append("g")
        .attr("transform", `translate(${margin.left}, 0)`)
        .call(d3.axisLeft(yScale));

    // if (displayData.length == 6) {
    //   svg.append("g")
    //     .attr("transform", `translate(${margin.left}, 0)`)
    //     .call(d3.axisLeft(yScale));
    // }

    svg.selectAll("circle")
      .data(displayData)
      .enter()
      .append("circle")
      .attr("cx", (_, i) => xScale(i))
      .attr("cy", d => yScale(d))
      .attr("r", circleRadius)
      .attr("fill", "#D6496B");
  }, [data, margin.top, margin.bottom, margin.left, margin.right]);

  return (
    <svg ref={svgRef} width={width} height={height}>
      {/* SVG content will be added by D3 */}
    </svg>
  );
};

export default Component;
